import { Box, LinearProgress, alpha, useTheme } from "@rp/react/web/mui/core"

const LineStep = ({ activeStep, index }: { activeStep: number; index: number }) => {
  const theme = useTheme()
  const opacity = index === activeStep ? 0.32 : 1
  const color =
    index <= activeStep ? alpha(theme.palette.primary.main, opacity) : theme.palette.elevation.dp24

  if (index <= activeStep) {
    return (
      <LinearProgress
        variant="determinate"
        value={index === activeStep ? -1 : 100}
        sx={{
          height: 4,
          width: "100%",
        }}
      />
    )
  }

  return (
    <Box
      sx={{
        backgroundColor: color,
        height: 4,
        width: "100%",
      }}
    />
  )
}

type Props = {
  steps: number
  activeStep: number
}

const VerticalScrollStepperBar = ({ steps, activeStep }: Props) => {
  return (
    <Box
      sx={{
        display: "flex",
        height: "4px",
        alignItems: "center",
        my: 1,
        gap: 1,
        "& div:nth-of-type(1)": { borderRadius: "4px 0 0 4px" },
        "& div:last-of-type": { borderRadius: "0 4px 4px 0" },
      }}
    >
      {new Array(steps).fill(0).map((_, index) => {
        return <LineStep key={index} activeStep={activeStep} index={index} />
      })}
    </Box>
  )
}

export { VerticalScrollStepperBar }
